

.contacts-wrapper__subtitile {
  font-family: @oswald;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  margin: 0;
  color: @black-grey;

  @media (min-width: @tablet) {
    font-size: 20px;
    line-height: 26px;
    width: 163px;
  }
}

.contacts-wrapper__address {
  margin: 0;
  line-height: 20px;

  @media (min-width: @tablet) {
    font-size: 16px;
    width: 175px;
    text-align: right;
  }
}
